    outline: none;
    float: right;
}


/* Class */

.navBar {
    width: 100%;
    color: black;
    padding: 10px;
}

.nav-links {
    float: right;
    overflow: hidden;
    list-style-type: none;
    font-weight: bold;
    margin: 12px;
    font-size: 20px;
}

.logo-galexy {
    width: 20px;
    height: auto;
    position: absolute;
    margin-top: 6px;
    margin-left: 8px;
    opacity: 0.7;
}

.show-nav {
    height: 120px;
}

.splash {
    display: inline;
    overflow: hidden;
    list-style-type: none;
    font-weight: bold;
    margin: 12px;
    font-size: 20px;
}


/* Media screen size */

@media screen and (min-width: 768px) {
    .nav-button {
        display: none;
    }
    .nav-links {
        height: auto;
        display: flex;
        margin: 0px;
    }
    li {
        padding: 0 15px;
    }
    .navBar {
        height: 20px;
    }
}
